@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #dcdcdc;
  position: relative;
  min-height: 100vh;
}

body, input, textarea, button {
  font-family: 'Rubik', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.main__div {
    height: 20px;
    width: 100%;
    background-color: #e0e0de;
    border-radius: 8px;
    margin-top: 2px;
}

.child__div {
  height: 100%;
  background: linear-gradient(90deg, rgba(162,219,150,1) 0%, rgba(36,204,169,1) 90%);
  border-radius: inherit;
  text-align: right;
}

.progressbar__text {
  padding: 5px;
  color: white;
  font-weight: bold;
  font-size: 12px;
}

.stat-name {
  font-size: 12px;
  margin-top: 5px;
}

.Card {
    background: white;
    border-radius: 10px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-width: 500px;
}

.Card__img {
    text-align: center;
}

.Card__name {
    text-align: center;
    text-transform: capitalize;
    font-weight: 800;
    font-size: 20px;
}

.Card__types {
    display: flex;
    justify-content: center;    
}

.Card__type {
    padding: 5px 10px;
    margin: 10px 2px 10px 2px;
    border-radius: 5px;
    background: cyan;
    color: #fff;
    text-transform: uppercase;
    font-size: 12px;
}

.Card__info {
    margin-left: 20px;
    margin-right: 20px;
}

.Card__data {
    padding-bottom: 15px;
}

.Card__basicdata {
    padding: 5px 10px;
    margin: 5px 5px 5px 5px;
    border-radius: 5px;
    text-align: center;
}

.Card__data__title {
    font-weight: 700;
}

p {
    margin: 0;
}
.navbar {
    display: flex;
    justify-content: center;
    padding: 20px;
    background: #363636;
    color: white;
}

.nav-image {
    width: 200px;
}
.searchbar-container {
    display: flex;
    margin: 25px 20px 25px 20px;
    padding: 0;
    align-items: center;
    flex: 1 1;
    align-items: stretch;
  }
  
  .searchbar {
    background: white;
    flex: 1 1;
    border-radius: 8px;
  }
  
  .searchbar input {
    padding: 16px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
    border-radius: 8px;
    border: none;
    width: 100%;
    font-size: 16px;
  }
  
  .searchbar input:focus {
    outline: none;
  }
  
  .searchbar-btn button {
    background-color: #363636;
    border: none;
    border-radius: 8px;
    height: 100%;
    padding: 10px 30px;
    margin-left: 10px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
    background-image: url("https://assets.pokemon.com/static2/_ui/img/chrome/forms/input-search-bg.png");
    background-position: center;
    background-repeat: no-repeat;
  }
.footer {
    width: 100%;
    position: absolute;
    bottom: 0;
    display: flex;
    flex-flow: wrap;
    justify-content: center;
    padding: 20px;
    background: #363636;
    color: white;
}

.footer span {
    width: 100%;
    display: flex;
    margin-bottom: 15px;
    justify-content: center;
    font-size: 14px;
}
* {
    box-sizing: border-box;
}

.app__main__div {
    padding-bottom: 6.5rem;
}

.grid-container {
    width: 80%;
    display: grid;
    margin: 0 auto;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 10px;
}

.btn {
    display: flex;
    justify-content: center;
    margin: 20px 0;
}

.btn button {
    border-style: none;
    padding: 10px 15px;
    margin: 0px 10px 10px 10px;
    border-radius: 5px;
    background: #30a7d7;
    color: #fff;
    font-size: 1em;

}

.center-loader {
    position: absolute;
    top: 50%; 
    left: 50%; 
    transform: translate(-55%, -50%);
}
